@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
/* Base styles */
* {
  margin: 0;
  font-family: "Roboto", sans-serif;
  color: #333;
}

/* Nav bar*/
.navbar {
  padding: 0px 20px 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 800px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
  font-size: 40px;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}

.content {
  padding: 40px 40px 40px 40px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  max-width: 800px;
  margin: auto;
}

.home button,
.online-game-setup button,
.join-game [type="submit"] {
  background-color: #f1356d;
  color: white;
  border: none;
  font-size: 20px;
  padding: 15px;
  margin: 5px;
  border-radius: 7px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  vertical-align: center;
  width: 200px;
}

.join-game label,
.join-game input {
  vertical-align: center;
  font-size: 20px;
  padding: 12px;
  margin: 5px;
  border-radius: 7px;
  margin-left: 20px;
}

.join-game input {
  width: 200px;
}

body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 2px solid #f1356d63;
  float: left;
  font-size: 50px;
  font-weight: bold;
  line-height: 34px;
  height: 70px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 70px;
}

.square.a0 {
  border-left: none;
  border-top: none;
}

.square.a1 {
  border-top: none;
}

.square.a2 {
  border-right: none;
  border-top: none;
}

.square.a3 {
  border-left: none;
}

.square.a5 {
  border-right: none;
}

.square.a6 {
  border-left: none;
  border-bottom: none;
}

.square.a7 {
  border-bottom: none;
}

.square.a7 {
  border-bottom: none;
}

.square.a8 {
  border-right: none;
  border-bottom: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.game-info {
  margin-left: 50px;
}

